.header {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: 1000px;
}

.header > * {
	color: black;
	transition: 0.3s;
}

.heading {
	font-size: 2em;
	font-weight: bold;
	margin: 15px 0 0;
	transition: 0.3s;
}
.heading-smaller {
	font-size: 1.5em;
	font-weight: bold;
	margin: 15px 0 0;
	transition: 0.3s;
}

.heading-middle {
	font-size: 1.2em;
	margin: 0px 5px;
	font-style: italic;
	transition: 0.3s;
}
.heading-right-container {
	display: flex;
	margin-right: 10px;
	transition: 0.3s;
}
.heading-right-menu {
	position: absolute;
	right: 20px;
	top: 60px;
	animation: myAnim 1s ease 0s 1 normal forwards;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.links {
	font-size: 1.2em;
	margin: 0 20px;
	position: relative;
	font-weight: 500;
	transition: 0.3s;
	text-decoration: none !important;
}

.links:hover {
	cursor: pointer;
}
.links::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 4px;
	border-radius: 4px;
	background-color: #18272f;
	bottom: 0;
	left: 0;
	transform-origin: right;
	transform: scaleX(0);
	transition: transform 0.3s ease-in-out;
	bottom: -5px;
}
.links:hover::before {
	transform-origin: left;
	transform: scaleX(1);
}

.footer {
	width: 100%;
	min-width: 300px;
	background-color: rgb(119, 89, 117);
	height: 150px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 250px;
	transition: 0.3s;
}

.inner-footer {
	max-width: 1000px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	position: relative;
	transition: 0.3s;
}

.copied {
	background-color: rgba(255, 255, 255, 0.575);
	color: black;
	height: 30px;
	width: 100%;
	position: absolute;
	top: -36px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	font-size: 0.8em;
	animation: myAnim 0.5s ease 0s 1 normal forwards;
}

@media (max-width: 1200px) {
	.heading {
		margin-left: 10px;
	}
	.heading-middle{
		margin-left: 15px;
	}
	.heading-right-container {
		margin-right: 15px;
	}
}

@media (max-width: 900px) {
	.header > * {
		font-size: 0.9em;
	}
	.links {
		font-size: 1em;
		margin: 5px 10px;
	}
	.footer {
		height: 270px;
	}
	.inner-footer {
		flex-direction: column;
		height: 80%;
		justify-content: space-around;
	}
}

@media (max-width: 400px) {
	.heading {
		font-size: 1.3em;
		margin-left: 5px;
		right: 0;
	}
	.heading-right-container {
		margin-right: 15px;
	}
	.links {
		font-size: 0.7em;
		margin: 5px 5px;
	}
	.links-menu {
		font-size: 0.7em;
		margin: 5px 5px;
	}
	.heading-middle{
		font-size: 0.8em;
		margin-left: 10px;
	}
	.footer {
		height: 250px;
	}
	.inner-footer {
		flex-direction: column;
	}
}
